import React from 'react'

function CreateCards(props) {
    return (
        <div className="cards" onClick={() => {
            props.shuffle()
            props.clicked()
            }} styles={{transition: "all 1s ease 1s"}} >
            <img src={props.img}/>
            <h4>{props.name}</h4>
        </div>
    )
}

export default CreateCards