import React, { useState, useEffect } from 'react'
import Loader from './Components/UI/Loader'
import Header from './Components/UI/Header'
import DisplayCards from './Components/Game Logic/DisplayCards'
import Score from './Components/Game Logic/Score'
import Restart from './Components/Game Logic/Restart'
function App() {
  const [ mount, setMount ] = useState(false)
  const [ clicked, setClicked ] = useState(true)
  const [ score, setScore ] = useState(0)
  const [ cardClicked, setCardClicked ] = useState([])
  const [ restart, setRestart ] = useState(false)

  function clickedCard() {
    setClicked(true)
  }

  function hideRestart() {
    setRestart(false)
    setScore(0)
  }

    useEffect(() => {
        setClicked(false)
        if(!clicked) {
          const cards = document.querySelectorAll('.cards')
          cards.forEach(card => {
              card.addEventListener('click', () => {
                  if(cardClicked.includes(card) === false) {
                      setCardClicked(cardClicked.concat(card))
                      setScore(score + 1)
                  }
                  else {
                    setRestart(true)
                    setScore(0)
                    setCardClicked([])
                  }
              })
          })
        }
    }, [clicked])

  useEffect(() => {
    setTimeout(() => {
      if(!mount) {
        setMount(true)
      }
    }, 2000)
    
  }, [mount])

  return (
    <div>
      <Loader display={mount} />
      <Header />
      <DisplayCards clicked={clickedCard}/>
      <Score score={score}/>
      <Restart restart={restart} score={score} hideRestart={hideRestart}/>
    </div>
  )
}


export default App